import React, {Component, lazy, Suspense} from 'react';
import {
	Badge,
	Card,
	CardBody,
	CardFooter,
	CardHeader,
	CardTitle,
	CardSubtitle,
	Col,
	Row,
	InputGroupAddon,
	InputGroup,
	InputGroupButton,
	Input,
	Button, CardImg, CardImgOverlay
} from 'reactstrap';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import 'react-bootstrap-table/dist//react-bootstrap-table-all.min.css';
import logo from '../../assets/img/brand/logo.png';

import axios from 'axios';

import {CustomTooltips} from '@coreui/coreui-plugin-chartjs-custom-tooltips';
import {getStyle, hexToRgba} from '@coreui/coreui/dist/js/coreui-utilities'
import Widget02 from '../../views/Widgets/Widget02'

const Loading = () => <div>Loading...</div>;

function imageFormatter(cell, row) {
	var url = row.photo_url + "";
	return '<img className="img-fluid" height="100%" width="100%" src=' + url + ' />';
}

class UserDashboard extends Component {
	constructor(props) {
		super(props);

		this.state = {
			data: [
				{name: "S", surname: "E", birthYear: 1988, birthCity: "Karaman"},
				{name: "SH", surname: "E", birthYear: 1988, birthCity: "Karaman"}
			],
			search: "",
			userID: sessionStorage.getItem("user_id"),
			original_restaurants: [],
			restaurants: [
				{
					rid: 1,
					cid: 1,
					cname: "pizza",
					name: "Metro Pizza",
					contact: "(702) 486-4368",
					location: "5757 Wayne Newton Blvd",
					star: 2.5,
					photo_url: "https:\/\/s3-media1.fl.yelpcdn.com\/bphoto\/aAoUthFqsND23zdy1sZftw\/o.jpg",
					longitude: -115.1391027,
					latitude: 36.0808997
				},
				{
					rid: 2,
					cid: 6,
					cname: "Caffe",
					name: "Buddy V's Ristorante E Caffe",
					contact: "(702) 424-5786",
					location: "3930 Las Vegas Blvd S",
					star: 3.5,
					photo_url: "https:\/\/s3-media4.fl.yelpcdn.com\/bphoto\/tpzXs9AmhxeZj7CcI2ZFRA\/o.jpg",
					longitude: -115.138026,
					latitude: 36.089494
				}
			],
			recommendRestaurants: [
				{
					rid: 1,
					cid: 1,
					cname: "pizza",
					name: "Metro Pizza",
					contact: "(702) 486-4368",
					location: "5757 Wayne Newton Blvd",
					star: 2.5,
					photo_url: "https:\/\/s3-media1.fl.yelpcdn.com\/bphoto\/aAoUthFqsND23zdy1sZftw\/o.jpg",
					longitude: -115.1391027,
					latitude: 36.0808997
				},
				{
					rid: 2,
					cid: 6,
					cname: "Caffe",
					name: "Buddy V's Ristorante E Caffe",
					contact: "(702) 424-5786",
					location: "3930 Las Vegas Blvd S",
					star: 3.5,
					photo_url: "https:\/\/s3-media4.fl.yelpcdn.com\/bphoto\/tpzXs9AmhxeZj7CcI2ZFRA\/o.jpg",
					longitude: -115.138026,
					latitude: 36.089494
				}
			],
			recommendEconomicRestaurants: []

		};
		this.viewMore = this.viewMore.bind(this);
		this.viewMoreRecommend = this.viewMoreRecommend.bind(this);
		this.viewMoreRecommend_eco = this.viewMoreRecommend_eco.bind(this);

		axios.get('http://localhost:8080/api/restaurant')
			.then(response => {
				this.setState({
					restaurants: response.data,
					original_restaurants: response.data
				});

			}, error => {
				console.log(error);
			});

		axios.get('http://localhost:8080/api/recommendation/' + this.state.userID)
			.then(response => {
				this.setState({
					recommendRestaurants: response.data
				});

			}, error => {
				console.log(error);
			});


		axios.get('http://localhost:8080/api/cheap-recommendation')
			.then(response => {
				this.setState({
					recommendEconomicRestaurants: response.data
				});

			}, error => {
				console.log(error);
			});
	}

	viewMore = rid => {
		//redirecting to restaurant details
		this.props.history.push({
			pathname: './restaurant',
			state: {restaurantID: rid}
		});
		sessionStorage.setItem("restaurantID", rid);
	};

	viewMoreRecommend = index => {
		//redirecting to recommend restaurant details
		this.props.history.push({
			pathname: './restaurant',
			state: {restaurantID: this.state.recommendRestaurants[index].rid}
		});
		sessionStorage.setItem("restaurantID", this.state.recommendRestaurants[index].rid);
	};

	viewMoreRecommend_eco = index => {
		//redirecting to recommend restaurant details
		this.props.history.push({
			pathname: './restaurant',
			state: {restaurantID: this.state.recommendEconomicRestaurants[index].rid}
		});
		sessionStorage.setItem("restaurantID", this.state.recommendEconomicRestaurants[index].rid);

	};

	search = e => {
		let restaurants = this.state.original_restaurants
			.filter(res => res.name.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1 ||
				res.cname.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1);
		this.setState({restaurants});
		window.scrollTo({
			top: 200,
			behavior: "smooth"
		})
	};

	input = e => {
		this.setState({search: e.target.value})
	};

	render() {
		return (
			<div style={{marginTop: "65vh"}}>
				<img src={logo} className="logo"/>
				<InputGroup className="search">
					<Input onChange={this.input} placeholder="search your favourite restaurants / category..."
						   type="text"/>
					<InputGroupButton
						onClick={this.search}
						addonType="append"
						style={{background: "orange", border: "none", color: "white"}}>Search</InputGroupButton>
				</InputGroup>
				<div className="banner"/>
				<div className="animated fadeIn">
					<Row>
						<Col xs="1" sm="1" lg="1"/>
						<Col xs="7" sm="7" lg="7">
							<h2 className="text-dark"><i className="fa fa-delicious"/> <b>Restaurants</b></h2>
							<Row>
								{this.state.restaurants.map(restaurant => (
									<Col lg="6" key={restaurant.rid}>
										<Card className="res" onClick={() => this.viewMore(restaurant.rid)}>
											<img className="res-photo" src={restaurant.photo_url}/>
											<CardBody>
												<CardTitle>
													<b className="res-title">{restaurant.name}</b>
												</CardTitle>
												<CardSubtitle className="res-subtitle">
													{restaurant.location}
												</CardSubtitle>
												<span
													className={`res-tag ${Math.random() < 0.5 ? 'bg-primary' : 'bg-warning'}`}>
													{restaurant.cname}
												</span>
											</CardBody>
										</Card>
									</Col>
								))}
							</Row>
						</Col>
						<Col xs="3" sm="3" lg="3">
							<h2 className="text-warning"><i className="fa fa-star"/> <b>Recommendation</b></h2>
							{this.state.recommendRestaurants.map((object, i) => {
								return <div key={i} onClick={this.viewMoreRecommend.bind(this, i)}>
									<Widget02 header={object.name} mainText={object.cname + " for you"}
											  icon="fa fa-star"
											  color="warning" footer/>
								</div>

							})}
							{this.state.recommendEconomicRestaurants.map((object, i) => {
								return <div key={i} onClick={this.viewMoreRecommend_eco.bind(this, i)}>
									<Widget02 header={object.name} mainText="High Quality & Low Price" icon="fa fa-fire"
											  color="danger" footer/>
								</div>
							})}
						</Col>
						<Col xs="1" sm="1" lg="1"/>
					</Row>
				</div>
			</div>
		);
	}
}

export default UserDashboard;
